import React from "react";
import { connect } from "react-redux";
import { Helmet } from 'react-helmet';
import { Card, CardBody, CardHeader, CardTitle, Row, Col } from "reactstrap"
import Breadcrumbs from "../../../../components/@vuexy/breadCrumbs/BreadCrumb"
import CategoryTypesTable from './Tables/CategoryTypesTable';
import CategoryTypesModal from './Modals/CategoryTypesModal';
import * as API from "../../../../utility/crud/app.crud";
import * as Date from "../../../../utility/dateAndTime";
import { toastr } from 'react-redux-toastr';

class CategoryTypes extends React.Component {
    constructor(props, context) {
        super(props, context);
        this.state = {
            columns: [],
            data: [],
            filteredData: [],
            value: '',
            show: false,
            showdelete: false,
            ID: 0,
            DESCRIPTION: '',
            CREATEDBYID: 1,
            LASTMODIFIEDBYID: 1,
            DATECREATED: Date.ShowDate(),
            DATELASTMODIFIED: Date.ShowDate(),
            InsertUpdate: ''

        }
        this.handleShow = () => {
            this.setState({
                show: true,
                InsertUpdate: 'Insert'
            });
            this.clearData();
        }

        this.handleHide = () => {
            this.setState({
                show: false
            });
        }

        this.handleHideDelete = () => {
            this.setState({
                showdelete: false
            });
        }

        this.populateData();
    }

    populateData() {
        const getdata = `
            query{
                CategoryTypes{
                    ID
                    DESCRIPTION
                    CREATEDBYID
                    LASTMODIFIEDBYID
                    DATECREATED
                    DATELASTMODIFIED
                }
            }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                data: data.data.CategoryTypes
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Data');
                }
            })

    }

    handleSubmit = (event) => {
        event.preventDefault();

        const form = `
        mutation{
            addCategoryType(
                DESCRIPTION:"${this.state.DESCRIPTION}",
                CREATEDBYID:${this.state.CREATEDBYID},
                LASTMODIFIEDBYID:${this.state.LASTMODIFIEDBYID},
                DATECREATED:"${this.state.DATECREATED}",
                DATELASTMODIFIED:"${this.state.DATELASTMODIFIED}"
                ){
                ID
            }
        }
        `;

        const formEdit = `
        mutation{
            updateCategoryType(
                ID:${this.state.ID},
                DESCRIPTION:"${this.state.DESCRIPTION}",
                LASTMODIFIEDBYID:${this.state.LASTMODIFIEDBYID},
                DATELASTMODIFIED:"${this.state.DATELASTMODIFIED}"
                ){
                ID
            }
        }
        `;
        //
        if (this.state.InsertUpdate === 'Insert') {
            API.create(form)
                .then(() => {
                    this.clearData();
                    this.handleHide();
                    this.populateData();
                    toastr.success('Create Record', 'Record Created Successfully');
                }).catch(error => {
                    if (error.response || error.request) {
                        toastr.error('Error Loading Data', 'Failed to Load Data');
                    }
                })
        } else {
            API.update(formEdit)
                .then(() => {
                    this.clearData();
                    this.handleHide();
                    this.populateData();
                    toastr.info('Update Record', 'Record Updated Successfully');
                }).catch(error => {
                    if (error.response || error.request) {
                        toastr.error('Error Loading Data', 'Failed to Load Data');
                    }
                })
        }

    }

    handleDelete = (event) => {
        event.preventDefault();

        const formDelete = `
        mutation{
            deleteCategoryType(
                ID:${this.state.ID}
                ){
                ID
            }
        }
        `;

        API.remove(formDelete)
            .then(() => {
                this.handleHideDelete();
                this.populateData();
                toastr.warning('Delete Record', 'Record Deleted Successfully');
            }).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Data');
                }
            })
    }

    handleEditData = (e) => {
        this.setState({
            ID: e.ID,
            DESCRIPTION: e.DESCRIPTION,
            show: true,
            InsertUpdate: 'Update'
        })
    }

    handleDeleteData = (e) => {
        this.setState({
            ID: e.ID,
            DESCRIPTION: e.DESCRIPTION,
            showdelete: true,
            InsertUpdate: 'Delete'
        })
    }

    clearData() {
        this.setState({
            DESCRIPTION: '',
        })
    }

    handleFilter = (e) => {
        let value = e.target.value
        let data = this.state.data
        let filteredData = this.state.filteredData
        this.setState({ value });

        if (value.length) {
            filteredData = data.filter(item => {
                let startsWithCondition =
                    item.DESCRIPTION.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.DESCRIPTION.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredData })
        }
    }

    render() {
        const { data, columns, value, filteredData } = this.state
        return (
            <>
                <Helmet><title>Category Types</title></Helmet>
                <React.Fragment>
                    <Breadcrumbs
                        breadCrumbTitle="Admin"
                        breadCrumbParent="Setup"
                        breadCrumbActive="Category Types"
                    />
                    <Row>
                        <Col sm="12">
                            <Card>
                                <CardHeader>
                                    <CardTitle>Category Types</CardTitle>
                                </CardHeader>
                                <CardBody className="rdt_Wrapper">

                                    <CategoryTypesTable
                                        role={this.constructor.name}
                                        handleEdit={this.handleEditData}
                                        handleDelete={this.handleDeleteData}
                                        value={value}
                                        data={data}
                                        columns={columns}
                                        filteredData={filteredData}
                                        handleShow={this.handleShow}
                                        handleFilter={this.handleFilter}
                                    />

                                    <CategoryTypesModal
                                        show={this.state.show}
                                        handleHide={this.handleHide}
                                        InsertUpdate={this.state.InsertUpdate}
                                        handleSubmit={this.handleSubmit}
                                        Description={this.state.DESCRIPTION}
                                        changeDescription={e => this.setState({ DESCRIPTION: e.target.value })}
                                        showdelete={this.state.showdelete}
                                        handleHideDelete={this.handleHideDelete}
                                        handleDelete={this.handleDelete}
                                    />


                                </CardBody>
                            </Card>
                        </Col>
                    </Row>

                </React.Fragment>
            </>
        )
    }

}

const mapStateToProps = ({ auth: { user } }) => ({
    user
});

export default (connect(mapStateToProps)(CategoryTypes));
